body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.dark-mode {
  background-color: black;
}

.dark-mode h1 {
  color: white;
}

.header-button {
  background-color: white;
  border-radius: 15px;
}

.header-button:hover {
  background-color: rgb(225, 238, 149);
  cursor: pointer;
}

.container {
  padding: 0px 15px 0px 15px;
  min-height: 100vh;
}

.search {
  display: flex;
  background-color: rgb(204, 205, 199);
  border-radius: 15px;
  padding: 5px;
}

.search input {
  display: flex;
  background-color: rgb(204, 205, 199);
  border: none;
  width: 100%;
}

.search input:focus {
  outline: none;
}

.notes-list {
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
}

.note {
  display: flex;
  background-color: rgb(247, 247, 136);
  padding: 1rem;
  border-radius: 10px;
  min-height: 170px;
  flex-direction: column;
  justify-content: space-between;
  white-space: pre-wrap;
}

.note.add {
  background-color: rgb(182, 159, 204);
}

textarea {
  background-color: transparent;
  border: none;
  resize: none;
}

textarea:focus {
  outline: none;
}

.note.add:hover {
  background-color: rgb(211, 169, 169);
}

.note:hover {
  background-color: lightyellow;
}

.note-footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.delete-icon {
  cursor: pointer;
}
